$("._tab").click(function() {
    let parentBlock = $(this).parents("._tabs-parent")
    let tabId = $(this).attr("data-tab")
    $(parentBlock).find("._tab").removeClass("_active")
    $(this).addClass("_active")
    $(parentBlock).find(".tab-content").removeClass("_active")
    $(parentBlock).find(`.${tabId}`).addClass("_active")
})

$(".qa-card__show").click(function() {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})

$("._js-cart-btn").click(function() {
    $(this).toggleClass("_added")
})

$(".favourite-btn").click(function() {
    $(this).toggleClass("_added")
})

$('.cartcalc').each(function (i,el) {
    let plus = $(el).find(".ccalc-plus")
    let minus = $(el).find(".ccalc-minus")
    let input = $(el).find("input")

    $(minus).click(function(e) {
        let a = $(input).val();
        if (a > 1) {
            let b = +a - 1;
            $(input).val(b);
            $(input).trigger('change');
        } else {
            $(input).val(a);
        }
        setMinusBtnClickability()
        e.stopImmediatePropagation()
    });

    $(plus).click(function(e) {
        let a = $(input).val();
        let b = +a + 1;
        $(input).val(b);
        $(input).trigger('change');
        setMinusBtnClickability(this)
        e.stopImmediatePropagation()
    });

    function setMinusBtnClickability() {
        let val = +$(input).val()
        if(val === 1){
            $(minus).addClass("_disabled")
        } else{
            $(minus).removeClass("_disabled")
        }
    }

    setMinusBtnClickability()
})

$(".review-card__more").click(function () {
    $(".review-modal__text").html(
        $(this).attr("data-text")
    )
    openModal("reviewModal")
})

$(document).on('click', '.rating-input__item', function(e) {
    let ratingItem = parseInt($(this).attr("data-rating-value"))
    $(this).parent(".rating-input").attr("data-total-rating", ratingItem)
    $(this).siblings(".rating-input__field").val(ratingItem)
})

function setLabelPlace(){
    let fields = document.querySelectorAll(".fg-dynamic input,.fg-dynamic textarea")

    fields.forEach(field => {
        setLabelPosition(field)

        field.onchange = ()=> {
            setLabelPosition(field)
        }

        function setLabelPosition() {
            if(field.value !== ""){
                field.classList.add("_filled")
            } else{
                field.classList.remove("_filled")
            }
        }
    })
}


setLabelPlace()


let nav = document.querySelector('.product-categories__items')

$(".product-categories__item").click(function () {
    $(".product-categories__item").removeClass("_active")
    $(this).addClass("_active")
    setActiveTabVisible()
})

function setActiveTabVisible() {
    if (window.innerWidth > 992) {
        return
    }
    if (nav) {
        let navLinks = document.querySelectorAll('.product-categories__item')
        let activeLink = document.querySelector('.product-categories__item._active')
        activeLink.scrollIntoView({
            behavior: "smooth",
            inline: "center",
            block: 'nearest',
        })
    }
}
